import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import styles from './Blog.module.css'
import Blogs from './Blogs/BlogData.js'
import BlogPage from './Blogs/BlogPage'

class Blog extends Component {
  constructor(props){
    super(props);
    this.state = {
      visibleRouter: true,
    }
    document.title = this.props.title;
  }
  render() {
    const { data } = Blogs;
    return (
      <div className={styles.pageContainer} >
        <h1 style={{ margin: 'unset' }}>My Blog</h1>
        <Router >
          <div className={styles.twoColumnContainer}>
            <div className={styles.leftColumnContainer}>
              {/* {window.location.pathname === '/blog' && */}
              {
              <div className={styles.blogList}>
                {data.map(page => {
                  return (
                    <div className={styles.blogItem}>
                      <Link 
                        onClick={() => { this.setState({ visibleRouter: false, }) }}
                        to={`/blog/${page.title}`}
                        className={styles.link}
                      >{page.title}</Link>
                    </div>
                  )
                })}
              </div>
              }
            </div>
            <div className={styles.rightColumnContainer}>
              {window.location.pathname !== '/blog' && data.map(page => {
                return (
                  <Route 
                    exact
                    path={`/blog/${page.title}`}
                    component={() => <BlogPage styleName={styles.blogContent} data={page} />} 
                  />
                )
              })}
            </div>
          </div>
        </Router>
      </div>
    );
  }
}

export default Blog;